export default {
	data() {
		return {
			devhistory:null
		}
	},
	onLoad:function(e){
		console.log("devId:"+e.devid);
		if(undefined != e.devid){
			uni.showLoading({
				title: '数据加载中...',
				mask: false
			});
			uni.request({
				url: this.globalVal.default_url.devHistory,
				method: 'POST',
				data: {
					deviceId:e.devid
				},
				success: res => {
					console.log(res);
					if(200 == res.statusCode){
						if(undefined == res.data.error_code){
							this.devhistory = res.data.deviceDataHistoryDTOs;
						}
					}
					
				},
				fail: () => {},
				complete: () => {
					uni.hideLoading();
				}
			});
		}
	},
	methods:{
	}
}
